
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'

import cookie from '../../../static/js/cookie'
const pulldownDefaultConfig = {
content: '下拉刷新',
height: 40,
autoRefresh: false,
downContent: '下拉刷新',
upContent: '释放后刷新',
loadingContent: '正在刷新...',
clsPrefix: 'xs-plugin-pulldown-'
}
const pullupDefaultConfig = {
content: '上拉加载更多',
pullUpHeight: 60,
height: 40,
autoRefresh: false,
downContent: '释放后加载',
upContent: '上拉加载更多',
loadingContent: '加载中...',
clsPrefix: 'xs-plugin-pullup-'
}
export default {
components: {
Search,
Scroller,
XHeader,
Divider,
TabItem,
Tab,
FormPreview
},
data () {
return {
note: '',
msgContent: [],
isActive: 1,
page: 1,
list: [],
pullupDefaultConfig: pullupDefaultConfig,
pulldownDefaultConfig: pulldownDefaultConfig
}
},
mounted () {
let header = document.getElementById('header').offsetHeight
let footer = document.getElementById('footer').offsetHeight
this.loadMore()
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top: 0})
})
window.msgContent = document.documentElement.clientHeight
this.msgContent = (window.msgContent - header - footer) + 'px'
window.onresize = () => {
return (() => {
window.msgContent = document.documentElement.clientHeight
this.msgContent = (window.msgContent - header - footer) + 'px'
})()
}
},
methods: {
getOrders (data) {
let mid = cookie.getCookie('mid')
return this.$api.orders({'page': this.page,'mid':mid}).then(res => {
this.$nextTick(() => {
this.$refs.scrollerBottom.reset()
})
data(res.data)
})
},
refresh () {
this.page = 1
this.getOrders(data => {
this.list = data.results
this.$refs.scrollerBottom.enablePullup()
this.$refs.scrollerBottom.donePulldown()
})
},
loadMore () {
this.getOrders(data => {
if (!data.next) {
this.$refs.scrollerBottom.disablePullup()
this.$refs.scrollerBottom.donePulldown()
this.isActive = 0
}
this.isActive = 0
this.page++
this.list = this.list.concat(data.results)
this.$refs.scrollerBottom.donePullup()
})
}
}
}



